section.home {
  margin-top: 4rem;
  text-align: center;
}

.home__image {
  width: 200px;
  margin: 1rem auto;
}

.home__buttons {
  display: flex;
  justify-content: center;
  gap: 2rem;
}

.home__button {
  text-align: center;
}

.button__image {
  width: 45%;
  margin: auto;
  padding: 6rem 0;
  border: 1px solid #333;
  background: linear-gradient(45deg, #0f589c, #2387c5, #0f589c);
  border-radius: 10px;
}
.home__button:last-child .button__image {
  background: linear-gradient(45deg, #3d7cb8, #c239b4, #3d7cb8);
}

.button__image img {
  width: 50%;
}

.home__button .btn::before {
  position: absolute;
  content: '';
  width: 100%;
  height: 0;
  bottom: 0;
  left: -100%;
  border-bottom: 2px solid var(--color-primary);
}
.home__button .btn::after {
  position: absolute;
  content: '';
  width: 100%;
  height: 0;
  bottom: 0;
  right: -300%;
  border-bottom: 2px solid var(--color-primary);
  transition: 1s all ease;
}

.home__button:hover .btn::before {
  left: 0;
  transition: var(--transition);
}
.home__button:hover .btn::after {
  right: 0;
}


/* news */
.news h2 {
  margin-bottom: 0;
}

.news .nav {
  height: 50px;
}

.blogs {
  margin: 1rem 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 2rem;
  align-items: flex-end;
}

.blog {
  width: 30%;
  flex-grow: 1;
}

.blog__image {
  width: 100%;
  height: 200px;
  background: linear-gradient(45deg, #3d7cb8, #c239b4, #3d7cb8);
}
.blog:nth-child(2n) .blog__image {
  background: linear-gradient(45deg, #0f589c, #2387c5, #0f589c);
}

.blog__title {
  color: var(--color-primary);
  font-size: 1.2rem;
}
.blog__text {
  color: var(--color-dark-variant);
}