.navbar {
  width: 100%;
  height: 50px;
  border-bottom: 1px solid #ccc;
  position: sticky;
  top: 0;
  background: var(--color-bg);
}

.nav {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav__items {
  display: flex;
  justify-content: flex-start;
  gap: 1rem;
}

.nav__item {
  color: var(--color-primary);
}